
.attention{position: relative; width: 6.46rem;min-height: 4.67rem;padding: 0.35rem 0 0.35rem 0.3rem; background: #fff;margin: 0 auto;border-radius: 0.08rem;-webkit-box-shadow: 0 0 0.06rem 0.06rem #eee;box-shadow: 0 0 0.06rem 0.06rem #eee;margin-top: 0.2rem;
}
.attention .a_aote{width: 0.22rem;height: 0.22rem;position: absolute;top: 0.2rem;right: 0.2rem;
}
.attention .a_aote img{width: 0.22rem;height: 0.22rem;
}
.attention_left{float: left;width: 1.18rem;height: 100%;
}
.attention_left span{width: 0.97rem;height: 0.97rem;
}
.attention_left span img{width: 0.97rem;height: 0.97rem;border-radius: 50%;
}
.attention_right{float: left;width :4.74rem;
}
.attention_right .att_top_attention{font-size: 0.25rem; margin-top: 0.2rem; float: right;width: 1.44rem;height: 0.54rem;background: #42c055;border-radius: 0.05rem;text-align: center;color: #fff;line-height: 0.54rem;
}
.attention_right .att_top_attentioner{font-size: 0.25rem;font-family: consolas;  margin-top: 0.2rem; float: right;width: 1.44rem;height: 0.54rem;background: #ccc;border-radius: 0.05rem;text-align: center;color: #fff;line-height: 0.54rem;
}
.attention_right p{font-size: 0.28rem;line-height: 0.4rem;font-family: "Microsoft YaHei";
}
.att_right_top{height: 1.03rem;
}
.attention_name h3{font-size: 0.32rem;line-height: 0.58rem;
}
.attention_name span{font-size: 0.2rem;line-height: 0.27rem;color: #8b8b8b;
}
.attention_right ul{margin-top: 0.24rem;
}
.attention_right ul li{float: left;width: 0.80rem;color:#999;padding-right: 0.2rem;margin-right: 0.07rem;
}
.attention_right ul li p{float: right;
}
.attention_right ul li img{width: 0.38rem;height: 0.38rem;
}





