
.musicWarg{background: #F7F7F7;overflow: hidden;min-height: 11rem;
}
.musicWarg .listinnerXing .listinnerXing_txt{font-size: 0.22rem;color: #999;margin-left: 0.05rem;
}
.musicWarg .filmImglist{min-width: 14.2rem;background: #fff;overflow: hidden;position: relative;-webkit-animation: filmImglist 8s;animation: filmImglist 8s;-webkit-animation-iteration-count: infinite;animation-iteration-count: infinite;-webkit-animation-direction: alternate;animation-direction: alternate;-webkit-transition-timing-function: linear;transition-timing-function: linear;
}
@-webkit-keyframes filmImglist{
from{-webkit-transform: translateX(0rem);transform: translateX(0rem);
}
to{-webkit-transform: translateX(-6.8rem);transform: translateX(-6.8rem);
}
}
@keyframes filmImglist{
from{-webkit-transform: translateX(0rem);transform: translateX(0rem);
}
to{-webkit-transform: translateX(-6.8rem);transform: translateX(-6.8rem);
}
}



