
.filmWarg{background: #F7F7F7;overflow: hidden;min-height: 11.85rem;padding-bottom: 0.95rem;
}
.filmWarg .filmImglist{width: 14.2rem;background: #fff;overflow: hidden;position: relative;-webkit-animation: filmImglist 15s 1s;animation: filmImglist 15s 1s;-webkit-animation-iteration-count: infinite;animation-iteration-count: infinite;-webkit-animation-direction: alternate;animation-direction: alternate;-webkit-transition-timing-function: linear;transition-timing-function: linear;
}
.filmWarg .filmImglister{-webkit-animation: filmImglist 18s 3s;animation: filmImglist 18s 3s;-webkit-animation-iteration-count: 2;animation-iteration-count: 2;-webkit-animation-direction: alternate;animation-direction: alternate;
}
.filmWarg .filmmoreWarg .filminner span p{position: relative;
}
.filmWarg .filmmoreWarg .filminner span .filmtxt{color: #999;margin-right: 0.15rem;
}
.filmWarg .filmmoreWarg .filminner span .maxImg{display: none;
}
.filmWarg .filmmoreWarg .filminner span img{height: 0.35rem;position: absolute;top: 0.35rem;right: -0.5rem;
}
@-webkit-keyframes filmImglist{
from{-webkit-transform: translateX(0rem);transform: translateX(0rem);
}
to{-webkit-transform: translateX(-6.6rem);transform: translateX(-6.6rem);
}
}
@keyframes filmImglist{
from{-webkit-transform: translateX(0rem);transform: translateX(0rem);
}
to{-webkit-transform: translateX(-6.6rem);transform: translateX(-6.6rem);
}
}
@-webkit-keyframes filmImglister{
from{-webkit-transform: translateX(0rem);transform: translateX(0rem);
}
to{-webkit-transform: translateX(-6.6rem);transform: translateX(-6.6rem);
}
}
@keyframes filmImglister{
from{-webkit-transform: translateX(0rem);transform: translateX(0rem);
}
to{-webkit-transform: translateX(-6.6rem);transform: translateX(-6.6rem);
}
}



