
.televisioninnerWarg{float: left;
}
.televisioninner{width: 5.6rem;height: 2.2rem;border: 1px solid #e1e1e1;margin-left: 0.3rem;padding: 0.2rem;
}
.televisioninner .tv_innerImg{width: 1.6rem;height: 2.24rem;float: left;margin-right: 0.3rem;
}
.televisioninner .tv_innerImg img{width: 1.6rem;height: 2.24rem;
}
.televisioninner .tv_innerText{float: left;
}
.televisioninner .tv_innerText .tv_innerTitle{line-height: 0.4rem;font-size: 0.33rem;
}
.televisioninner .listinnerXing ul{margin: 0.1rem 0;
}
.televisioninner .listinnerXing ul li{float: left;
}
.televisioninner .listinnerXing img{width: 0.23rem;
}
.televisioninner .listinnerXing .listinnerXing_txt{font-size: 0.20rem;color: #9b9b9b;
}
.televisioninner .tv_innerJI{font-size: 0.26rem;color: #9b9b9b;float: left;line-height: 0.33rem;width: 3.59rem;
}
.televisioninner .tv_innerLook{font-size: 0.20rem;color: #9b9b9b;line-height: 0.33rem;
}



