
.audiotWarg{min-height: 11.85rem;padding-top: 1.66rem;
}
.audiotWarg .ulactive {background: #fff; width: 100%;height: 0.76rem;border-bottom: 1px solid #e4e4e4;position: fixed;left: 0;top: 1.25rem;z-index: 20;
}
.audiotWarg .ulactive li{width: 20%;float: left;height: 0.72rem;text-align: center;line-height: 0.72rem;
}
.audiotWarg .ulactive li a{color:#999;text-decoration: none;font-size: 0.23rem;
}
.audiotWarg .ulactive .active{border-bottom: 0.04rem solid #66bd76;
}
.audiotWarg .ulactive .active a{color:#66bd76;
}
.audiotWarg .filmWarg{background: #F7F7F7;
}
.audiotWarg .seekAll .homeTops{background: #F7F8F7;color: #999;
}
.audiotWarg .seekAll .homeTops span{color:#999;
}



