
.minepersonal{width: 100%;height: 1.95rem;background: #5dc76d;padding-top: 0.55rem;color: #fff;
}
.minepersonal .mine_name{float: left;height: 100%;width: 2.1rem;
}
.minepersonal .mine_name img{margin-left: 0.4rem;width: 1.32rem;height: 1.32rem;border-radius: 50%;border: 0.05rem solid #fff;
}
.minepersonal .mine_ming{border-bottom: 1px solid #caeccf;width: 5.10rem;float: left;height: 1.05rem;
}
.minepersonal .mine_ming h2{font-size: 0.36rem;line-height: 0.6rem;font-weight: normal;
}
.minepersonal .mine_ming span{font-size: 0.22rem;line-height: 0.4rem;
}
.minepersonal .mine_ming h3{font-size: 0.22rem;float: right;font-weight: normal;line-height: 0.4rem;margin: 0.52rem 0.1rem 0 0;
}
.minepersonal .mine_ming h3 img{float: right;height: 0.33rem;
}
.minepersonal ul li{float: left;line-height: 0.7rem;padding-right: 0.45rem;font-size: 0.24rem;
}



