
.groupinnerWarg{width: 100%;
}
.groupinnerWarg .groupinnerImg{height: 3.03rem;width: 100%;position: relative;
}
.groupinnerWarg .groupinnerImg .grou_img{height: 3.03rem;width: 100%;
}
.groupinnerWarg .groupinnerImg .groupinnerTxt{height: 3.03rem;width: 100%;text-align: center;position: absolute;top: 1.13rem;left: 0;
}
.groupinnerWarg .groupinnerImg .groupinnerTxt h4{text-align: center;font-size: 0.33rem;line-height: 0.46rem;
}
.groupinnerWarg .groupinnerImg .groupinnerTxt span{font-size: 0.28rem;color:#999;line-height: 0.37rem;
}
.groupinnerWarg .groupinnerImg .recommend{font-size: 0.22rem;color:#999;line-height: 0.4rem;position: absolute;left: 0.29rem;bottom: 0;
}
.groupinnerWarg .gro_inner{width: 6.72rem;height: 1.17rem;background: #66d77b;border-radius: 0.09rem;margin: 0 auto;margin-top: 0.47rem;position: relative;
}
.groupinnerWarg .gro_inner span{color:#fffcfa;font-size:0.26rem ;line-height: 0.48rem;margin: 0.69rem 0 0 0.24rem;display: inline-block;
}
.groupinnerWarg .gro_inner img{width: 1.36rem;height: 1.32rem;float: right;position: absolute;right: 0.87rem;top: -0.15rem;
}
.groupinnerWarg .grou_film{height: 0.67rem;width: 6.72rem;border-bottom: 1px solid #ccc;margin: 0 auto 0.27rem auto;text-align: center;
}
.groupinnerWarg .grou_film h2{font-size: 0.26rem; width: 1.35rem;height: 0.4rem;line-height: 0.4rem;background: #fff;display: inline-block;position: relative;bottom: -0.5rem;
}



