
.televisionWarg{overflow: hidden;background: #F7F7F7;min-height: 12.35rem;
}
.televisionWarg .televisionWarg_bottom{padding-bottom: 0.7rem;
}
.televisionWarg .televisionWargbann{min-width: 14.2rem;background: #fff;overflow: hidden;position: relative;-webkit-animation: televisionWargbann 5s;animation: televisionWargbann 5s;-webkit-animation-iteration-count: 2;animation-iteration-count: 2;-webkit-animation-direction: alternate;animation-direction: alternate;-webkit-transition-timing-function: linear;transition-timing-function: linear;
}
.televisionWarg .televisionbottom{-webkit-animation: televisionbottom 8s 2s;animation: televisionbottom 8s 2s;-webkit-animation-iteration-count: infinite;animation-iteration-count: infinite;-webkit-animation-direction: alternate;animation-direction: alternate;
}
@-webkit-keyframes televisionWargbann{
from{-webkit-transform: translateX(0rem);transform: translateX(0rem);
}
to{-webkit-transform: translateX(-6.1rem);transform: translateX(-6.1rem);
}
}
@keyframes televisionWargbann{
from{-webkit-transform: translateX(0rem);transform: translateX(0rem);
}
to{-webkit-transform: translateX(-6.1rem);transform: translateX(-6.1rem);
}
}
@-webkit-keyframes televisionbottom{
from{-webkit-transform: translateX(0rem);transform: translateX(0rem);
}
to{-webkit-transform: translateX(-6.7rem);transform: translateX(-6.7rem);
}
}
@keyframes televisionbottom{
from{-webkit-transform: translateX(0rem);transform: translateX(0rem);
}
to{-webkit-transform: translateX(-6.7rem);transform: translateX(-6.7rem);
}
}



