
.minewarn{height: 1.93rem;background: #fff;padding-left: 0.25rem;margin: 0.3rem 0;text-align: center;
}
.minewarninner{border-bottom: 1px solid #e4e4e4;height: 1.04rem;margin-left: 0.08rem;
}
.minewarninner .warn_rightimg{float: right;height: 0.49rem;margin-top: 0.32rem;
}
.minewarninner .warn_leftimg{width: 0.32rem;height: 0.38rem;float: left;margin-top: 0.32rem;
}
.minewarninner h2{float: left;margin:0.32rem 0 0 0.32rem;line-height: 0.43rem;font-size: 0.24rem;
}
.minewarn span{line-height: 0.74rem;color:#999;font-size: 0.22rem;height: 0.74rem;display: block;
}



