
.topheaderWarg{height: 0.4rem;width: 100%;
}
.tast_top{height: 0.4rem;width: 100%;text-align: center;background: #F7F8F7;color:black;position: fixed;top:0;left: 0;z-index: 11;
}
.tast_top h4{float: left;line-height: 0.4rem;margin: 0 0.1rem;font-size: 0.22rem;
}
.tast_top span{float: left;line-height: 0.4rem;margin: 0 0.1rem;font-size: 0.22rem;
}
.tast_top ul li{width: 0.09rem;height: 0.09rem;float: left;margin: 0.12rem 1px;background: black;border-radius: 50%;border:1px solid black;
}
.tast_top ul .xinhao2{background: transparent;
}
.tast_top .time{width: 32%;font-size: 0.22rem;
}
.tast_top .electric{float: right;font-size: 0.22rem;
}
.tast_top .electric img{margin: 0.01rem 0.07rem 0 2px;height: 0.18rem;
}



