
.cityWarg{background: #f7f7f7;min-height: 11.85rem;padding-bottom: 0.95rem;overflow: hidden;
}
.cityWarg .listinnerXing .listinnerXing_txt{padding: 0 0.06rem;height: 0.25rem;border: 0.02rem solid #ff4155;color: #ff4155; border-radius: 0.05rem;line-height: 0.25rem;font-size: 0.22rem;text-align: center;
}
.cityWarg .filmImglist{height: 4.60rem; min-width: 14.2rem;background: #fff;overflow: hidden;position: relative;-webkit-animation: filmImglist 8s;animation: filmImglist 8s;-webkit-animation-iteration-count: 3;animation-iteration-count: 3;-webkit-animation-direction: alternate;animation-direction: alternate;-webkit-transition-timing-function: linear;transition-timing-function: linear;
}
.cityWarg .filmlistWarg .listinner .listinnerTxtsan{height: 0.57rem;overflow: hidden;
}
.cityWarg .filmImglister{-webkit-animation: filmImglister 8s 3s;animation: filmImglister 8s 3s;-webkit-animation-iteration-count: infinite;animation-iteration-count: infinite;-webkit-animation-direction: alternate;animation-direction: alternate;-webkit-transition-timing-function: ease-in-out;transition-timing-function: ease-in-out;
}
@-webkit-keyframes filmImglist{
from{-webkit-transform: translateX(0rem);transform: translateX(0rem);
}
to{-webkit-transform: translateX(-6.8rem);transform: translateX(-6.8rem);
}
}
@keyframes filmImglist{
from{-webkit-transform: translateX(0rem);transform: translateX(0rem);
}
to{-webkit-transform: translateX(-6.8rem);transform: translateX(-6.8rem);
}
}
@-webkit-keyframes filmImglister{
from{-webkit-transform: translateX(0rem);transform: translateX(0rem);
}
to{-webkit-transform: translateX(-6.8rem);transform: translateX(-6.8rem);
}
}
@keyframes filmImglister{
from{-webkit-transform: translateX(0rem);transform: translateX(0rem);
}
to{-webkit-transform: translateX(-6.8rem);transform: translateX(-6.8rem);
}
}



