
.seekAll{position: fixed;top: 0.4rem;left: 0;z-index: 22; padding-bottom: 0.95rem;background: #F7F7F7;height:11.85rem;width: 100%;
}
.seekAll .homeTops{background: #42C055;position: relative; z-index: 14;width: 100%;
}
.seekAll .homeTops .texts{width:6.24rem;height: 0.58rem;text-indent: 2em;border-radius: 0.05rem;border:none;margin: 0.13rem;font-size: 0.24rem;
}
.seekAll .homeTops .searchImgs{top: 0.27rem;left: 0.25rem;width: 0.36rem;position: absolute;
}
.seekAll .homeTops .scanImgs{top: 0.27rem;right: 1rem;width: 0.36rem;position: absolute;
}
.seekAll .homeTops span{color: #FFF;font-size: 0.25rem;
}
.seekAll p{height: 0.9rem;line-height: 0.9rem;font-size: 0.25rem;color:#999;margin-left: 0.3rem;
}
.seekAll .lists{width: 100%;
}
.seekAll .lists li{font-size: 0.26rem;font-weight: bold;font-family: "Microsoft YaHei"; background: #fff; padding: 0 0.2rem;height: 0.98rem;line-height: 0.98rem;border-bottom: 0.01rem solid #e4e4e4;
}
.seekAll .lists li span{color:#999; font-size: 0.22rem;font-family: "Microsoft YaHei";font-weight: normal;margin:0 0.2rem;
}



