
.studyWarg{overflow: hidden;background: #f7f7f7;min-height:  10.83rem;
}
.studyWarg .studyWarginner{min-width:  14.2rem;background: #fff;overflow: hidden;position: relative;-webkit-animation: studyWarginner 8s;animation: studyWarginner 8s;-webkit-animation-iteration-count: infinite;animation-iteration-count: infinite;-webkit-animation-direction: alternate;animation-direction: alternate;-webkit-transition-timing-function: linear;transition-timing-function: linear;
}
@-webkit-keyframes studyWarginner{
from{-webkit-transform: translateX(0rem);transform: translateX(0rem);
}
to{-webkit-transform: translateX(-6.2rem);transform: translateX(-6.2rem);
}
}
@keyframes studyWarginner{
from{-webkit-transform: translateX(0rem);transform: translateX(0rem);
}
to{-webkit-transform: translateX(-6.2rem);transform: translateX(-6.2rem);
}
}



